import './../../assets/style.scss';
import {Navbar,Nav} from 'react-bootstrap';
import {
  BrowserRouter as Router,
  Switch,
  Route,
} from "react-router-dom";
import {Link} from 'react-scroll'

const NavbarC = () => {
  return (
    <Navbar collapseOnSelect expand="lg" variant="dark">
      <Navbar.Brand href="#home">Ahmad algshy</Navbar.Brand>
      <Navbar.Toggle aria-controls="responsive-navbar-nav" />
      <Navbar.Collapse id="responsive-navbar-nav">
        <Nav className="ml-auto">
          
          <Link className="nav-link" spy={true} smooth={true} to="home">Home</Link>
          <Link className="nav-link" spy={true} smooth={true} to="projects">Projects</Link>
          <Link className="nav-link" spy={true} smooth={true} to="partners">Partners</Link>
          <Link className="nav-link" spy={true} smooth={true} to="contact">Contact</Link>
  
        </Nav>
      </Navbar.Collapse>
    </Navbar>
  );
}

export default NavbarC;
