import './../../assets/style.scss';




const Paragraph = (props) => {
  return (
    <div className="paragraphCon">
    < div className="circle"></div>
    < div className="text">
      <h3 className="title">
        {props.title}
      </h3>
      <p className="paragraph">
        {props.children}
      </p>      
    </div>
    </div>
  );
}

export default Paragraph;
