import React,{useEffect} from 'react';
import './../assets/style.scss';
import { Container, Row, Col, Button } from 'react-bootstrap';
import { Facebook,Instagram } from 'react-bootstrap-icons';
import AOS from 'aos';
import "aos/dist/aos.css";

import businessman from './../assets/images/business-3d.png'



const Contact = () => {
  useEffect(() => {
    AOS.init({
      duration : 1000
    });
  },[]);
  return (
    <div className="contact">
      <Container>
        <Row>
          <Col md="6">
            <div className="title">
              <span>Contact me</span>
              <div className="brownCircle"></div>
            </div>
            <div className="paragraph">
              Whenever, wherever. We're meant to work
              together.
            </div>
            <form>
              <input type="text" placeholder="Full name" />
              <input type="text" placeholder="Full name" />
              <input type="text" placeholder="Full name" />
              <textarea rows="4" type="text" placeholder="Message" />
              <Button type="submit">Submit</Button>
            </form>
          </Col>
          <Col md="6" className="contact-pic-con" data-aos="zoom-in-up" data-aos-delay="300">
            <div className="brown-circle"></div>
            <img className="contact-pic" src={businessman} />
          </Col>
        </Row>
        <div className="media">
          <div className="circle"><Facebook /></div>
          <div className="circle"><Instagram /></div>
        </div>
      </Container>
    </div>
  );
}

export default Contact;
