import './../../assets/style.scss';




const BigTitle = (props) => {
  return (
    <div className="bigTitle">
      <div className="bigCircle"></div>
      <div className="bgText">{props.title}</div>
      <div className="text">
        <h1 className="title">
          {props.title}
        </h1>
        <p className="paragraph">
          {props.children}
        </p>
      </div>
      
    </div>
  );
}

export default BigTitle;
