import React from 'react';
import partner1 from './../../assets/images/partner1.png'

const PartnerIcon = (props) => {
    return (
        <div className="partnerIcon">
            <div className="circleImgCon">
                <img src={partner1} />
            </div>
            <div className="partnerInfo">
                <div className="partnerName">
                    {props.name}
                </div>
                <div className="partnerJob">
                    {props.job}
                </div>
            </div>
        </div>
    );
};

export default PartnerIcon;