import React, { useEffect } from 'react';
import './../assets/style.scss';
import { Container, Row, Col, Button } from 'react-bootstrap';
import Example from './../assets/images/example.png';

import BigTitle from './../components/bigTitle/bigTitle';
import Paragraph from './../components/paragraph/paragraph';
import { Facebook, Instagram } from 'react-bootstrap-icons';
import { ScrollTrigger } from "gsap/ScrollTrigger";
import gsap from 'gsap'






const SpecificProject = () => {
  if (typeof window !== "undefined") {
    gsap.registerPlugin(ScrollTrigger);
  }
  useEffect(() => {
    gsap.to(".rightTop", {
      duration: 1,
      y:200,
      scrollTrigger: {
        trigger: ".rightTop",
        // pin: true,
        scrub: 3,
        start: "top 90%",
        // scale:3,
        // snap: 1 / (3000 - 1),
        // end: () => "+=" + document.querySelector(".home-pic-con .brown-circle").offsetWidth
      }
    })
    gsap.to(".leftTop", {
      duration: 1,
      y:400,
      scrollTrigger: {
        trigger: ".leftTop",
        // pin: true,
        scrub: 3,
        start: "top 50%",
        // scale:3,
        // snap: 1 / (3000 - 1),
        // end: () => "+=" + document.querySelector(".home-pic-con .brown-circle").offsetWidth
      }
    })
    gsap.to(".rightMiddle", {
      duration: 1,
      y:200,
      scrollTrigger: {
        trigger: ".rightMiddle",
        // pin: true,
        scrub: 3,
        start: "top 20%",
        // scale:3,
        // snap: 1 / (3000 - 1),
        // end: () => "+=" + document.querySelector(".home-pic-con .brown-circle").offsetWidth
      }
    })
    gsap.to(".leftMiddle", {
      duration: 3,
      y:200,
      scrollTrigger: {
        trigger: ".leftMiddle",
        // pin: true,
        scrub: 3,
        start: "top 90%",
        // scale:3,
        // snap: 1 / (3000 - 1),
        // end: () => "+=" + document.querySelector(".home-pic-con .brown-circle").offsetWidth
      }
    })
    gsap.to(".rightBottom", {
      duration: 1,
      y:200,
      scrollTrigger: {
        trigger: ".rightBottom",
        // pin: true,
        scrub: 3,
        start: "top 90%",
        // scale:3,
        // snap: 1 / (3000 - 1),
        // end: () => "+=" + document.querySelector(".home-pic-con .brown-circle").offsetWidth
      }
    })
    gsap.to(".leftBottom", {
      duration: 1,
      y:200,
      scrollTrigger: {
        trigger: ".leftBottom",
        // pin: true,
        scrub: 3,
        start: "top 90%",
        // scale:3,
        // snap: 1 / (3000 - 1),
        // end: () => "+=" + document.querySelector(".home-pic-con .brown-circle").offsetWidth
      }
    })
  })
  return (
    <div className="specificProject">
      <div className="parallaxCircle rightTop">
      </div>
      <div className="parallaxCircle leftTop">
      </div>
      <div className="parallaxCircle rightMiddle">
      </div>
      <div className="parallaxCircle leftMiddle">
      </div>
      <div className="parallaxCircle rightBottom">
      </div>
      <div className="parallaxCircle leftBottom">
      </div>
      <Container>
        <BigTitle title="How I do it">
          I will take your idea and help you out to reach the stars.You only need to dream it and i will make it possible.
        </BigTitle>
        <Paragraph title="Meeting with the client">
          The stepping stone in our process. Come visit, or skype with us. Let’s discuss your idea over a cup of coffee and get to know each other. Details matter, we want to know it all. Tell us your requirements and we will handle the management.
        </Paragraph>
        <Paragraph title="Research">
          I went around and ask people what they want to make their daily commute easier. Out of the varied responses and the statistics we managed to draw an outline for the application.
        </Paragraph>
        <img className="img luminosity" src={Example} />
        <Paragraph title="Sketching">
          I went around and ask people what they want to make their daily commute easier. Out of the varied responses and the statistics we managed to draw an outline for the application.
        </Paragraph>
        <img className="img" src={Example} />
        <Paragraph title="Interactive design">
          This one is on us. We’ll create the initial draft and diagrams that set your idea in motion. We scheme, we plan, we come up with the concept that would garner success. The low fidelity gives way to high fidelity, then the UI/UX is established and is ready to be implemented.
        </Paragraph>
        <img className="img" src={Example} />
        <Paragraph title="Testing">
          The most important phase after finishing with the code. We’ll catch all the nasty bugs that might have slipped during development, squish them, and ensure everything works as intended.
        </Paragraph>
        <div className="contactMe"><div className="circle"></div><div className="text">Contact me</div></div>
        <div className="media">
          <div className="circle"><Facebook /></div>
          <div className="circle"><Instagram /></div>
        </div>
        <div className="designed">
          Designed by @Ahmadalgshy
        </div>
      </Container>
    </div>
  );
}

export default SpecificProject;
