import './../../assets/style.scss';

import CircleTitle from './../circleTitle/circleTitle'
import {Link} from 'react-router-dom'



const ProjectCard = (props) => {
    let withLink =props.link?"withLink":null;
  return (
    <Link to="/specificProject/5" className={"projectCard "+withLink}>
        <CircleTitle size="sm">{props.title}</CircleTitle>
        <div className="des">{props.children}</div>
        <div className="date">{props.date}</div> 
        {
            props.link?<a className="link" href={props.link}>Get in touch</a>:null
        }
    </Link>
  );
}

export default ProjectCard;
