import React,{useState} from 'react';

import PartnerIcon from './partnerIcon'


const Slider = () => {
    const [active,setActive] = useState(0);
    const [partners,setPartners] = useState([
        {
            'name':'HARALDUR THORLEIFSSON',
            'job':'CEO at Ueno',
            'paragraph':'Ahmad is everything you want in a good creative. He is great at his craft, he is a great designer, he can tell stories through his work, he can make prototypes, he can animate. But more importantly, he is a fantastic team player. He happily picks up any task that needs to get done, he brings energy and joy to any room, he works extremely hard and will always go the extra mile. I can’t say enough good things about Robin, he is a wonderful person, a great designer, and a true team player.'
        },
        {
            'name':'ahmad odeh',
            'job':'front-end',
            'paragraph':'Ahmad is great worker and he has a big nose '
        },
        {
            'name':'anas sasa',
            'job':'front-end',
            'paragraph':'Ahmad is great worker and he has a big nose '
        },
    ]);
    const next = () =>{
        if(active!=partners.length-1)
            setActive(active+1);
    }
    const prev = () =>{
        if(active!=0)
            setActive(active-1);
    }
    return (
        <div className="slider">
            <div className="arrows">
                <button className="arrowBtn" onClick={prev}>
                <svg xmlns="http://www.w3.org/2000/svg" width="22" height="22" fill="currentColor" class="bi bi-arrow-left" viewBox="0 0 16 16">
                    <path fill-rule="evenodd" d="M15 8a.5.5 0 0 0-.5-.5H2.707l3.147-3.146a.5.5 0 1 0-.708-.708l-4 4a.5.5 0 0 0 0 .708l4 4a.5.5 0 0 0 .708-.708L2.707 8.5H14.5A.5.5 0 0 0 15 8z"/>
                </svg>
                </button>
                <div className="sliderPages">
                    {active+1+"/"+partners.length}
                </div>
                <button className="arrowBtn" onClick={next}>
                <svg xmlns="http://www.w3.org/2000/svg" width="22" height="22" fill="currentColor" class="bi bi-arrow-right" viewBox="0 0 16 16">
                    <path fill-rule="evenodd" d="M1 8a.5.5 0 0 1 .5-.5h11.793l-3.147-3.146a.5.5 0 0 1 .708-.708l4 4a.5.5 0 0 1 0 .708l-4 4a.5.5 0 0 1-.708-.708L13.293 8.5H1.5A.5.5 0 0 1 1 8z"/>
                </svg>
                </button>
            </div>
            <PartnerIcon name={partners[active].name} job={partners[active].job}/>
            <div className="paragraph">
                {partners[active].paragraph}
            </div>
        </div>
    );
};

export default Slider;